import {goWindow} from '@/api/navigate.js'  // 页面跳转
import {request} from '@/api/reques.js'
 export default {
        data() {
            return {
               option:{},
            };
        },
        methods: {
			// 获取用户手机号
			getphonenumber(e){
				if(e.detail.errMsg.split(":")[1] !== "ok"){
					uni.navigateBack({
					   delta: 1
					});
				}else{
					let _this = this;
					let encryptedData = e.detail.encryptedData;
					let iv = e.detail.iv;
					// 1.wx获取登录用户code
					uni.login({
					  provider: 'weixin',
					  success(loginRes) {
						 //2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
					    request('/login/getWechatInfo',{
					    	code:loginRes.code,
					    },'POST')
						.then((res)=>{
							let sessionKey = JSON.parse(res.data.data).session_key;
							//console.log(sessionKey)
							request("/login/getWechatInfos",{
								sessionKey: sessionKey,
								encryptedData: encryptedData,
								iv:iv
							},'POST')
							.then((res)=>{
								//console.log(res)
								let phone = JSON.parse(res.data.data).phoneNumber
								//console.log(phone)
								request("/member/bindMemberMobile",{
									token:uni.getStorageSync('token'),
									mobile:phone
								},"POST")
								.then((res)=>{
									if(res.data.data > 0){
										let pages = getCurrentPages()
										wx.navigateBack({
											success(){
												pages[pages.length-2].onLoad(_this.option)
											}
										});
									}
									//console.log(res)
								})
							})
						})
					  }
					});
					
						
				}
			}
				
        },
		
        onLoad(option) {//默认加载
			this.option = option;
			//console.log(option)
			let _this = this;
            let toKen = uni.getStorageSync('token');
			if(toKen !== ''){
				request("/member/isbindmobile",{
						token:uni.getStorageSync('token'),
					},"POST")
					.then((res)=>{
						if(res.data.data === 1){
							let pages = getCurrentPages()
							wx.navigateBack({
							    success(){
									pages[pages.length-2].onLoad()
								}
							});
						}
					})
			    }else{
					goWindow('/pages/common/login')
			    }
			}
	}
